<h1 id="android-menus" tabindex="-1"><a class="header-anchor" href="#android-menus" aria-hidden="true">#</a> Android Menus</h1>
<p>Android has built-in support for different type of menus. In this lecture we take a look at two of them: the options menu and the floating context menu.</p>
